//
// Buttons
// --------------------------------------------------

// Base class
.btn {
    display: inline-block;
    position: relative;
    height: $btn-height;
    margin: {
        top: floor(calc($btn-margin / 1.5));
        right: $btn-margin;
        bottom: floor(calc($btn-margin / 1.5));
    }
    padding: 0 22px;
    transform: translateZ(0);
    transition: all 0.4s;
    border: 1px solid transparent;
    border-radius: floor(calc($btn-height / 2));
    background: {
        color: transparent;
        image: none;
    }
    color: $btn-text-color;
    font: {
        family: $font-family-base;
        size: $btn-font-size;
        style: normal;
        weight: $btn-font-weight;
    }
    letter-spacing: 0.07em;
    line-height: ($btn-height - 2);
    white-space: nowrap;
    cursor: pointer;
    vertical-align: middle;
    text: {
        transform: $btn-text-transform;
        decoration: none;
        align: center;
    }
    touch-action: manipulation;
    @include user-select(none);

    &:hover,
    &:focus &:active,
    &.active {
        outline: none;
        background-image: none;
        text-decoration: none;
        box-shadow: none;
    }

    &:hover {
        color: $btn-text-color;
    }

    > i {
        display: inline-block;
        margin-top: -2px;
        vertical-align: middle;
        font-size: 1rem;
    }

    &[disabled],
    &.disabled {
        cursor: not-allowed;
        opacity: 0.55;
        pointer-events: none;
    }
}

button:focus {
    outline: none;
}

// Button Sizes
.btn-lg {
    height: $btn-lg-height;
    border-radius: floor(calc($btn-lg-height / 2));
    line-height: ($btn-lg-height - 2);
}

.btn-sm {
    height: $btn-sm-height;
    padding: 0 18px;
    border-radius: floor(calc($btn-sm-height / 2));
    font-size: floor(calc($font-size-base * 0.75));
    line-height: ($btn-sm-height - 2);
}

// Button Shapes
.btn-rounded {
    border-radius: $border-radius-base;

    &.btn-lg {
        border-radius: $border-radius-lg;
    }

    &.btn-sm {
        border-radius: $border-radius-sm;
    }
}

.btn-square {
    border-radius: 0;
}

// Filled Button Variants
// Uses @mixin button-variant($bg-color)
.btn-secondary {
    @include button-variant($gray-lighter);
    border-color: $border-color;
}

.btn-primary,
.btn-success,
.btn-info,
.btn-warning,
.btn-danger {
    color: $white-color;

    &:hover,
    &:active,
    &:focus {
        color: $white-color;
    }
}

.btn-primary {
    @include button-variant($brand-primary);
}

.btn-success {
    @include button-variant($brand-success);
}

.btn-info {
    @include button-variant($brand-info);
}

.btn-warning {
    @include button-variant($brand-warning);
}

.btn-danger {
    @include button-variant($brand-danger);
}

.btn-white {
    @include button-variant($white-color);
}

// Outline Button Variants
// Uses @mixin outline-button-variant($color)
.btn-outline-secondary {
    border-color: $border-color;

    &:hover {
        background-color: $gray-lighter;
    }
}

.btn-outline-primary {
    @include outline-button-variant($brand-primary);
}

.btn-outline-success {
    @include outline-button-variant($brand-success);
}

.btn-outline-info {
    @include outline-button-variant($brand-info);
}

.btn-outline-warning {
    @include outline-button-variant($brand-warning);
}

.btn-outline-danger {
    @include outline-button-variant($brand-danger);
}

.btn-outline-white {
    @include outline-button-variant($white-color);

    &:hover {
        color: $btn-text-color;
    }
}

// Link Button Variants
// Uses @mixin link-button-variant($color)
.btn-link-secondary {
    @include link-button-variant($gray-dark);
}

.btn-link-primary {
    @include link-button-variant($brand-primary);
}

.btn-link-success {
    @include link-button-variant($brand-success);
}

.btn-link-info {
    @include link-button-variant($brand-info);
}

.btn-link-warning {
    @include link-button-variant($brand-warning);
}

.btn-link-danger {
    @include link-button-variant($brand-danger);
}

.btn-link-white {
    @include link-button-variant($white-color);
}

.btn-link-secondary,
.btn-link-primary,
.btn-link-success,
.btn-link-info,
.btn-link-warning,
.btn-link-danger,
.btn-link-white {
    > i {
        margin-top: -4px;
    }
}

// Button Groups (Dropdowns)
.btn-group {
    display: inline-flex;
    position: relative;
    margin: {
        top: floor(calc($btn-margin / 1.5));
        right: $btn-margin;
        bottom: floor(calc($btn-margin / 1.5));
    }
    vertical-align: middle;

    .btn {
        position: relative;
        flex: 0 1 auto;
        margin: 0;

        &:first-child:not(:last-child):not(.dropdown-toggle) {
            margin-right: -1px;
            padding-right: 12px;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;

            &.btn-secondary,
            &.btn-outline-secondary {
                border-right: 0;
            }
        }
    }

    > .dropdown-toggle:not(:first-child) {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
}

.btn + .dropdown-toggle-split {
    padding: {
        right: 15px;
        left: 8px;
    }
}

// Alignment
.text-center {
    .btn,
    .btn-group {
        margin: {
            right: calc($btn-margin / 2);
            left: calc($btn-margin / 2);
        }
    }
}

.text-right {
    .btn,
    .btn-group {
        margin: {
            right: 0;
            left: $btn-margin;
        }
    }
}

// Block Buttons
.btn-block {
    display: block;
    width: 100%;
    margin: {
        top: floor(calc($grid-vertical-step / 2)); //~12px;
        right: 0 !important;
        bottom: floor(calc($grid-vertical-step / 2)); //~12px
        left: 0 !important;
    }
    padding: {
        right: 15px !important;
        left: 15px !important;
    }
}

// Scroll to Top Button
.scroll-to-top-btn {
    display: block;
    position: fixed;
    right: 16px;
    bottom: -($btn-height * 2);
    width: $btn-height;
    height: $btn-height;
    transition: bottom 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55), opacity 0.3s, background-color 0.3s,
        border-color 0.3s;
    border-radius: 50%;
    background-color: rgba($black-color, 0.3);
    color: $white-color;
    font-size: $font-size-lead;
    line-height: ($btn-height - 5);
    opacity: 0;
    z-index: 2000;
    text: {
        align: center;
        decoration: none;
    }

    &:hover {
        background-color: rgba($black-color, 0.8);
        color: $white-color;
    }

    &:focus,
    &:active {
        color: $white-color;
    }

    &.visible {
        bottom: 14px;
        opacity: 1;
    }

    @media (max-width: $screen-md) {
        bottom: -($btn-sm-height * 2);
        width: $btn-sm-height;
        height: $btn-sm-height;
        line-height: ($btn-sm-height - 5);
    }
}

// Market Buttons
.market-button {
    display: inline-block;
    margin: {
        right: 14px;
        bottom: 14px;
    }
    padding: 5px 14px 5px 45px;
    transition: background-color 0.3s;
    border: 1px solid $border-color;
    border-radius: $border-radius-base;
    background: {
        position: center left 12px;
        color: $white-color;
        size: 24px 24px;
        repeat: no-repeat;
    }

    text-decoration: none;

    &:hover {
        background-color: $gray-lighter;
    }

    .mb-subtitle {
        display: block;
        margin-bottom: -4px;
        color: $gray;
        font-size: floor(calc(($font-size-base / 1.33))); //~12px
    }

    .mb-title {
        display: block;
        color: $gray-dark;
        font-size: $font-size-lead;
    }

    &.apple-button {
        background-image: url(data:image/svg+xml;utf8;base64,);
    }

    &.google-button {
        background-image: url(data:image/svg+xml;utf8;base64,);
    }

    &.windows-button {
        background-image: url(data:image/svg+xml;utf8;base64,);
    }

    &.blackberry-button {
        background-image: url(data:image/svg+xml;utf8;base64,);
    }

    &.amazon-button {
        background-image: url(data:image/png;base64,);
    }

    &.mb-light-skin {
        border-color: $border-light-color;
        background-color: transparent;

        .mb-subtitle,
        .mb-title {
            color: $white-color;
        }

        .mb-subtitle {
            opacity: 0.55;
        }

        &:hover {
            background-color: rgba($white-color, 0.06);
        }

        &.apple-button {
            background-image: url(data:image/svg+xml;utf8;base64,);
        }

        &.blackberry-button {
            background-image: url(data:image/svg+xml;utf8;base64,);
        }

        &.amazon-button {
            background-image: url(data:image/png;base64,);
        }
    }
}

// Media Buttons
.facebook-btn,
.twitter-btn,
.google-btn {
    text-transform: none;

    > i {
        margin-top: 0;
    }
}

.facebook-btn {
    border-color: #3b5998;
    background-color: transparent;
    color: #3b5998;

    &:hover {
        background-color: #3b5998;
        color: $white-color;
    }
}

.twitter-btn {
    border-color: #55acee;
    background-color: transparent;
    color: #55acee;

    &:hover {
        background-color: #55acee;
        color: $white-color;
    }
}

.google-btn {
    border-color: #dd4b39;
    background-color: transparent;
    color: #dd4b39;

    > i {
        font-size: 1.3em;
    }

    &:hover {
        background-color: #dd4b39;
        color: $white-color;
    }
}
