//
// Social Buttons
// --------------------------------------------------

/* Base class */
.social-button {
    display: inline-block;
    margin: {
        top: 5px;
        right: 18px;
        bottom: 5px;
    }
    transition: color .3s;
    color: $body-color;
    font-size: $social-btn-size;
    text-decoration: none;
    vertical-align: middle;

    &.shape-circle,
    &.shape-rounded,
    &.shape-square {
        width: $btn-sm-height;
        height: $btn-sm-height;
        margin-right: 8px;
        border: 1px solid $border-color;
        line-height: $btn-sm-height - 1;
        text-align: center;
    }

    &.shape-circle {
        border-radius: 50%;
    }

    &.shape-rounded {
        border-radius: $border-radius-base;
    }

    &:focus {
        text-decoration: none;
    }
}

.text-center {
    .social-button {
        margin: {
            right: 9px;
            left: 9px;
        }

        &.shape-circle,
        &.shape-rounded,
        &.shape-square {
            margin: {
                right: 4px;
                left: 4px;
            }
        }
    }
}

.text-right {
    .social-button {
        margin: {
            right: 0;
            left: 18px;
        }

        &.shape-circle,
        &.shape-rounded,
        &.shape-square {
            margin: {
                right: 0;
                left: 8px;
            }
        }
    }
}

