// Light skin
.sb-light-skin {
    transition: all .3s;
    background-color: transparent;
    color: rgba($white-color, .6) !important;

    &:hover, &:focus {
        color: $white-color !important;
    }

    &.shape-circle,
    &.shape-rounded,
    &.shape-square {
        border-color: $border-light-color;

        &:hover {
            background-color: rgba($white-color, .06);
        }
    }
}
