    opacity: 1
}

.jp-card.jp-card-identified .jp-card-back .jp-card-logo, .jp-card.jp-card-identified .jp-card-front .jp-card-logo {
    box-shadow: 0 0 0 2px rgba(255, 255, 255, .3)
}

