//
// Forms
// --------------------------------------------------

.form-control {
    padding: 0 18px 3px;
    border: 1px solid darken($border-color, 2%);
    border-radius: ceil(calc($input-height / 6)) !important;
    background-color: $white-color;
    color: $gray-dark;
    font: {
        family: $font-family-base;
        size: $font-size-sm;
    }
    @include appearance(none);

    &:not(textarea) {
        height: $input-height;
    }

    // Placeholder
    @include placeholder;

    // Focus state
    &:focus {
        border-color: $brand-primary;
        outline: none;
        color: $gray-dark;
        box-shadow: none !important;
    }

    &[type='color'] {
        padding-bottom: 0 !important;
    }

    // Disabled
    &:disabled, &[readonly] {
        background-color: $gray-lighter;
        cursor: not-allowed;
    }

    &:valid {
      background-position : 97%;
    }

    &:invalid {
      background-position : 97%;
    }

}

// Textarea
textarea.form-control {
    padding: {
        top: 12px;
        bottom: 12px;
    }
}

// Select
select.form-control {
    padding-right: 38px;
    background: {
        position: center right 17px;
        image: url(data:image/svg+xml;utf8;base64,);
        repeat: no-repeat;
        size: 9px 9px;
    }

    &:not([size]):not([multiple]) {
        height: $input-height;
    }
}

// Form group
.form-group {
    margin-bottom: floor(calc($grid-vertical-step / 1.2)) !important; //~20px
    label {
        margin-bottom: 8px;
        //padding-left: 18px;
        font: {
            size: $font-size-xs;
            weight: 500;
        }
        cursor: pointer;

        &.col-form-label {
            align-self: center !important;
            margin-bottom: 0;
        }
    }
}

// Input sizes
.form-control-lg {
    border-radius: ceil(calc($input-height-lg / 2));
    font-size: $font-size-base;

    &:not(textarea) {
        height: $input-height-lg;
    }

    &.form-control-rounded {
        border-radius: $border-radius-lg;
    }

    &.form-control-square {
        border-radius: 0;
    }
}

select.form-control.form-control-lg {
    &:not([size]):not([multiple]) {
        height: $input-height-lg;
    }
}

.form-control-sm {
    padding-bottom: 2px;
    border-radius: ceil(calc($input-height-sm / 2));

    &:not(textarea) {
        height: $input-height-sm;
    }

    &.form-control-rounded {
        border-radius: $border-radius-sm;
    }

    &.form-control-square {
        border-radius: 0;
    }
}

select.form-control.form-control-sm {
    &:not([size]):not([multiple]) {
        height: $input-height-sm;
    }
}

// Form text
.form-text {
    padding-left: 18px;
}

// Checkboxes & Radio buttons
.custom-control {
    margin-bottom: 6px !important;
    padding-left: 1.5rem !important;

    &:focus {
        outline: 0;
    }

    .custom-control-label {
        margin: 0;
        padding-left: 0;
        font: {
            size: $font-size-sm !important;
            weight: normal !important;
        }
        text-transform: none;

        &::before {
            box-shadow: none !important;
        }
    }

    .custom-control-input:checked ~ .custom-control-label::before {
        border-color: $brand-primary;
        background-color: $brand-primary;
    }

    .custom-control-input:disabled ~ .custom-control-label {
        color: $gray;
        cursor: not-allowed;
    }
}

.custom-control-inline {
    align-items: center;
}

// File input
.custom-file,
.custom-file-input {
    height: $input-height;
    border-radius: ceil(calc($input-height / 6));
    cursor: pointer;

    &:focus ~ .custom-file-label {
        box-shadow: none;
    }
}

.custom-file-label {
    font-weight: normal !important;
}

.custom-file-label,
.custom-file-label::after {
    height: $input-height;
    border-radius: ceil(calc($input-height / 6));
    border-color: darken($border-color, 2%);
    color: $gray-dark;
    line-height: 2.2;
}

.custom-file-label::after {
    height: ($input-height - 2);
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    background-color: $gray-lighter;
    font-weight: 500 !important;
}

// Input shapes
.form-control-rounded {
    border-radius: $border-radius-base;

    .custom-file-control,
    .custom-file-control::before {
        border-radius: $border-radius-base;
    }
}

.form-control-square {
    border-radius: 0;

    .custom-file-control,
    .custom-file-control::before {
        border-radius: 0;
    }
}

// Input group
.input-group {
    display: block;
    position: relative;

    .input-group-addon,
    .input-group-btn {
        display: inline-block;
        position: absolute;
        top: 50%;
        margin-top: -3px;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
        z-index : 9;
    }

    .input-group-addon {
        left: 15px;
        transition: color .3s;
        background-color: transparent !important;
        color: $gray;
    }

    .form-control {
        padding-left: 35px;

        &:focus ~ .input-group-addon {
            color: $brand-primary;
        }
    }

    .input-group-btn {
        right: 10px;
        margin-top: -2px;

        button {
            transition: color .3s;
            border: 0;
            background: 0;
            color: $gray-dark;
            font-size: 1.2em;
            cursor: pointer;

            &:hover {
                color: $brand-primary;
            }
        }

        .btn {
            margin: 0;
            margin-right: 3px;
            padding: 0;
        }
    }

    .input-group-btn ~ .form-control {
        padding: {
            right: 35px;
            left: 18px;
        }
    }
}

// Light Skin
.input-light {
    &.form-control,
    .form-control {
        border-color: rgba($white-color, .17);
        background-color: transparent;
        color: $white-color;
        @include placeholder(rgba($white-color, .5));

        &:focus {
            border-color: $brand-primary;
            color: $white-color;
        }
    }

    &.input-group {
        .input-group-addon {
            color: rgba($white-color, .5);
        }
    }
}

// Subscription form
.subscribe-form {
    .input-group {
        width: calc(100% - 88px);
        float: left;
    }

    .btn {
        margin: 0;
        float: right;

        > i {
            margin-top: -6px;
            font-size: 1.5em;
        }
    }

    .form-text {
        padding-top: floor(calc($grid-vertical-step / 2));
    }
}

// Coupon form
.coupon-form {
    .form-control {
        display: inline-block;
        width: 100%;
        max-width: 235px;
        margin-right: 12px;
    }

    .btn {
        margin-right: 0;
    }

    @media (max-width: $screen-md) {
        .form-control {
            display: block;
            max-width: 100%;
        }
    }
}

// Login Box
.login-box {
    display: block;
    width: 100%;
    padding: 30px 24px;
    border: 1px solid $border-color;
    border-radius: $border-radius-lg;
}

// Interactive Credit Card
.card-wrapper {
    margin: 30px 0;
}

@media (max-width: $screen-sm) {
    .jp-card-container {
        width: 285px !important;
    }
    .jp-card {
        min-width: 250px !important;
    }
}

// Validation states
.was-validated {
    .form-control,
    .form-control-light-skin {
        &:valid,
        &.is-valid {
          border-color        : darken($border-color, 1%) !important;

          &:focus {
            border-color : $brand-primary !important;
          }

          background-position : 95%;
        }

        &:invalid,
        &.is-invalid {
          border-color : $brand-danger !important;

          &:focus {
            border-color : $brand-primary !important;
          }
        }

    }

    .custom-file-input:valid ~ .custom-file-label,
    .custom-file-input.is-valid ~ .custom-file-label {
        border-color: darken($border-color, 1%) !important;
    }

    .custom-control-input {
        &:valid,
        &.is-valid {
            & ~ .custom-control-label {
                color: $body-color;

                &::before {
                    background-color: #dee2e6;
                }
            }

            &:checked {
                & ~ .custom-control-label {
                    color: $body-color;

                    &::before {
                        border-color: $brand-primary;
                        background-color: $brand-primary;
                    }
                }
            }
        }
    }
}

.valid-feedback,
.invalid-feedback {
    margin-top: 5px;
    padding-left: 18px;
}

.custom-control {
    .valid-feedback,
    .invalid-feedback,
    .valid-tooltip,
    .invalid-tooltip {
        margin-left: 0;
        padding-left: 0;
    }
}

.valid-tooltip,
.invalid-tooltip {
    margin: {
        top: -1px;
        left: 18px;
    }
    padding: {
        top: 2px;
        bottom: 2px;
    }
    border-radius: 0;
    font-size: 90%;
}
