//
// Footer
// --------------------------------------------------

.site-footer {
    padding-top: floor(calc($grid-vertical-step * 3)); //~72px;
    background-color: $gray-darker;
    @media (max-width: $screen-md) {
        padding-top: floor(calc($grid-vertical-step * 2)); //~48px;
    }
}

.footer-copyright {
    margin: 0;
    padding: {
        top: 10px;
        bottom: $grid-vertical-step; // ~24px
    }
    color: rgba($white-color, .5);
    font: {
        size: $font-size-sm;
        weight: normal;
    }

    > a {
        transition: color .25s;
        color: rgba($white-color, .5);
        text-decoration: none;

        &:hover {
            color: $link-hover-color;
        }
    }
}

// Light Footer
.footer-light {
    background-color: $gray-lighter;

    .footer-copyright {
        color: $gray;

        > a {
            color: $gray;

            &:hover {
                color: $link-hover-color;
            }
        }
    }
}
