//
// Progress Bars
// --------------------------------------------------

.progress {
    height: auto;
    border-radius: ceil(calc($progress-height / 2));
    background-color: darken($gray-lighter, 2%);
    font: {
        size: $font-size-xs;
        weight: 500;
    }
    line-height: $progress-height;
}

.progress-bar {
    height: $progress-height;
    background-color: $progress-bg;
}
